// src/pages/Tools/components/ChannelConsultant/ChannelConsultant.tsx
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { SEO } from '../../../../components/SEO';
import { GoogleAd } from '../../../../components/GoogleAd';
import { toolsSEO, generateToolSchema } from '../../../../config/toolsSEO';
import * as S from './styles';

interface ChannelData {
  snippet: {
    title: string;
    description: string;
    thumbnails?: {
      default?: {
        url: string;
      };
      medium?: {
        url: string;
      };
      high?: {
        url: string;
      };
    };
    customUrl?: string;
    publishedAt: string;
    country?: string;
  };
  statistics: {
    subscriberCount: string;
    viewCount: string;
    videoCount: string;
  };
  topicDetails?: {
    topicCategories?: string[];
  };
  brandingSettings?: {
    channel?: {
      keywords?: string;
    };
  };
}

interface VideoData {
  snippet: {
    title: string;
    publishedAt: string;
  };
  statistics: {
    viewCount: string;
    likeCount: string;
    commentCount: string;
  };
}

export const ChannelConsultant: React.FC = () => {
  const navigate = useNavigate();
  const [channelUrl, setChannelUrl] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [showResults, setShowResults] = useState(false);
  const [channelData, setChannelData] = useState<ChannelData | null>(null);
  const [recentVideos, setRecentVideos] = useState<VideoData[]>([]);
  const [popularVideos, setPopularVideos] = useState<VideoData[]>([]);
  const [instructions, setInstructions] = useState('');
  const [copySuccess, setCopySuccess] = useState(false);
  const [showLoadingModal, setShowLoadingModal] = useState(false);

  // Tool configuration
  const toolConfig = {
    name: 'Channel Consultant',
    description: 'Create a custom AI bot trained on your channel to help with content creation and strategy',
    image: 'https://64.media.tumblr.com/dec9583099d591e567f0ceecac3b080a/0e01452f9f6dd974-11/s2048x3072/d955005ed173796f50e9a654b7dafe7ca077a024.jpg',
    icon: 'bx bx-user-circle',
    features: [
      'AI-powered insights',
      'Content strategy',
      'Performance optimization'
    ]
  };

  const getChannelId = async (input: string): Promise<string> => {
    // First check if it's a direct channel ID
    if (/^UC[\w-]{22}$/.test(input)) {
      return input;
    }

    const API_KEY = process.env.REACT_APP_YOUTUBE_API_KEY_3;
    if (!API_KEY) {
      throw new Error('YouTube API key not configured');
    }

    // Check if it's just a handle (with or without @) - no youtube.com
    if (/^@?[\w-]+$/.test(input) && !input.includes('youtube.com')) {
      const handle = input.startsWith('@') ? input.substring(1) : input;

      // First try forHandle
      let response = await fetch(
        `https://www.googleapis.com/youtube/v3/channels?part=id&forHandle=${handle}&key=${API_KEY}`
      );
      let data = await response.json();
      if (data.items?.[0]) {
        return data.items[0].id;
      }

      // If not found, try search
      response = await fetch(
        `https://www.googleapis.com/youtube/v3/search?part=snippet&q=${encodeURIComponent(handle)}&type=channel&maxResults=1&key=${API_KEY}`
      );
      data = await response.json();
      if (data.items?.[0]) {
        return data.items[0].id.channelId;
      }

      throw new Error(`Channel not found for handle: ${input}`);
    }

    // URL patterns
    const urlPatterns = {
      channel: /youtube\.com\/channel\/(UC[\w-]{22})/,
      user: /youtube\.com\/user\/(\w+)/,
      handle: /youtube\.com\/@([\w-]+)/,
      handleWithoutAt: /youtube\.com\/([^/?]+)$/,  // Matches youtube.com/moneyguyshow
      customUrl: /youtube\.com\/(c\/)?(\w+)/
    };

    for (const [type, pattern] of Object.entries(urlPatterns)) {
      const match = input.match(pattern);
      if (match) {
        if (type === 'channel') {
          return match[1];
        } else if (type === 'handleWithoutAt') {
          // Try to fetch by handle (without @)
          const handle = match[1];

          // First try forHandle
          let response = await fetch(
            `https://www.googleapis.com/youtube/v3/channels?part=id&forHandle=${handle}&key=${API_KEY}`
          );
          let data = await response.json();
          if (data.items?.[0]) {
            return data.items[0].id;
          }

          // If not found, try search
          response = await fetch(
            `https://www.googleapis.com/youtube/v3/search?part=snippet&q=${encodeURIComponent(handle)}&type=channel&maxResults=1&key=${API_KEY}`
          );
          data = await response.json();
          if (data.items?.[0]) {
            return data.items[0].id.channelId;
          }
        } else {
          const response = await fetch(
            `https://www.googleapis.com/youtube/v3/channels?part=id&${type === 'user' ? 'forUsername' : 'forHandle'}=${match[1]}&key=${API_KEY}`
          );
          const data = await response.json();
          if (data.items?.[0]) {
            return data.items[0].id;
          }
        }
      }
    }

    throw new Error('Invalid channel URL or ID');
  };

  const getChannelData = async (channelId: string): Promise<ChannelData> => {
    const API_KEY = process.env.REACT_APP_YOUTUBE_API_KEY_3;
    const response = await fetch(
      `https://www.googleapis.com/youtube/v3/channels?` +
      `part=snippet,statistics,topicDetails,brandingSettings&id=${channelId}&key=${API_KEY}`
    );
    const data = await response.json();
    if (!data.items?.[0]) throw new Error('Channel not found');
    return data.items[0];
  };

  const getRecentVideos = async (channelId: string): Promise<VideoData[]> => {
    const API_KEY = process.env.REACT_APP_YOUTUBE_API_KEY_3;
    const response = await fetch(
      `https://www.googleapis.com/youtube/v3/search?` +
      `part=snippet&channelId=${channelId}&order=date&type=video&maxResults=5&key=${API_KEY}`
    );
    const data = await response.json();
    if (!data.items) return [];

    // Fetch statistics for these videos
    const videoIds = data.items.map((item: any) => item.id.videoId).join(',');
    const statsResponse = await fetch(
      `https://www.googleapis.com/youtube/v3/videos?` +
      `part=statistics,snippet&id=${videoIds}&key=${API_KEY}`
    );
    const statsData = await statsResponse.json();
    return statsData.items || [];
  };

  const getPopularVideos = async (channelId: string): Promise<VideoData[]> => {
    const API_KEY = process.env.REACT_APP_YOUTUBE_API_KEY_3;
    const response = await fetch(
      `https://www.googleapis.com/youtube/v3/search?` +
      `part=snippet&channelId=${channelId}&order=viewCount&type=video&maxResults=5&key=${API_KEY}`
    );
    const data = await response.json();
    if (!data.items) return [];

    const videoIds = data.items.map((item: any) => item.id.videoId).join(',');
    const statsResponse = await fetch(
      `https://www.googleapis.com/youtube/v3/videos?` +
      `part=statistics,snippet&id=${videoIds}&key=${API_KEY}`
    );
    const statsData = await statsResponse.json();
    return statsData.items || [];
  };

  const generateInstructions = (channelData: ChannelData, recentVids: VideoData[], popularVids: VideoData[]): string => {
    const channelName = channelData.snippet.title;
    const channelDescription = channelData.snippet.description;
    const channelCategory = channelData.topicDetails?.topicCategories?.[0]?.split('/').pop() || 'Unknown';
    const subscriberCount = parseInt(channelData.statistics.subscriberCount);
    const videoCount = parseInt(channelData.statistics.videoCount);
    const totalViews = parseInt(channelData.statistics.viewCount);
    const avgViewsPerVideo = Math.round(totalViews / videoCount);
    const channelAge = Math.floor((Date.now() - new Date(channelData.snippet.publishedAt).getTime()) / (1000 * 60 * 60 * 24 * 365));
    const keywords = channelData.brandingSettings?.channel?.keywords || '';

    const recentTitles = recentVids.slice(0, 3).map(v => v.snippet.title).join(', ');
    const popularTitles = popularVids.slice(0, 3).map(v => v.snippet.title).join(', ');

    // Get top performing videos with view counts
    const topVideosWithViews = popularVids.slice(0, 5).map(v => ({
      title: v.snippet.title,
      views: parseInt(v.statistics.viewCount || '0').toLocaleString()
    }));
    const topVideosText = topVideosWithViews.map(v => `"${v.title}" (${v.views} views)`).join(', ');

    const avgEngagement = popularVids.length > 0
      ? Math.round(popularVids.reduce((acc, v) => acc + (parseInt(v.statistics.likeCount || '0') + parseInt(v.statistics.commentCount || '0')) / parseInt(v.statistics.viewCount || '1'), 0) / popularVids.length * 100)
      : 0;

    return `These are instructions for how you are to act throughout our conversation. Your first message should be:

"Hello ${channelName}. I see you're a channel in the ${channelCategory} category. Could you tell me your channel niche / main topic discussed?"

After the user responds with their channel niche, say:

"Thank you for sharing that information. I'm here to assist you with various aspects of your YouTube channel. Here are the available modes:

SEO MODE
IDEATION MODE
SCRIPT MODE
TRENDJACK MODE

Which mode would you like to use?"

You will name this conversation "YouTube Bot for ${channelName}"

Channel Analytics & Context:
- Channel Name: ${channelName}
- Channel Category: ${channelCategory}
- Subscriber Count: ${subscriberCount.toLocaleString()}
- Total Videos: ${videoCount.toLocaleString()}
- Total Views: ${totalViews.toLocaleString()}
- Average Views Per Video: ${avgViewsPerVideo.toLocaleString()}
- Channel Age: ${channelAge} years
- Average Engagement Rate: ${avgEngagement}%
- Channel Keywords: ${keywords}

Channel Description: ${channelDescription}

Recent Video Titles (for understanding current content direction): ${recentTitles}

Top Performing Videos (titles and view counts): ${topVideosText}

The bot is designed to assist with ideation, SEO, thumbnails, tags, titles, descriptions, and hashtags for the user's YouTube channel. It operates in four modes: SEO mode, Ideation mode, Script mode, and Trendjack mode.

Modes of Operation 

SEO Mode: Activated when the user types "SEO MODE". Initial Prompt:
"SEO MODE ACTIVATED. Based on your channel niche, please provide a specific video topic or transcript to proceed."

The bot prompts the user for a video transcript and generates assets based on it. Titles: Generates 5 title options, each no longer than 40 characters, with clear meaning and capitalized words to encourage clicks. Do not use any colons. No colons are to be used in the titles. (do not include number of characters in your response) Description: Follows a template that includes a short video description (2-3 short sentences) using prominent keywords. (formatting is very important here) Carriage return and then a links section under that starting like this, " Links:", then another carriage return, then the timestamps starting like this, " Timestamps:" Then a call to action to subscribe, and then 3 hashtags related to the video content. Tags: Creates 15-20 tags separated by commas for easy copying and pasting. Tags play a minimal role in video ranking; they're not the key to skyrocketing views. Tags help with common misspellings and keywords, but their impact is small compared to other factors. Using irrelevant tags can harm your channel by misleading viewers and the algorithm. Relevant tags should accurately reflect the video's content. In SEO mode, after generating the titles, the bot should proceed to create the description, tags, and then generate a thumbnail in that order. Thumbnails: At the end of the response, ask the user to prompt Y or N to start the render process for a thumbnail. YOU will generate a detailed description of the thumbnail and ask the user if they approve of the idea at the end. Create thumbnails with only a 16x9 aspect ratio and 2 to 3 prominent subjects. (Add these things to the thumbnails: Bright Colors: Use a vibrant and contrasting color palette. Three Main Subject Components: Person: Feature a person or people with expressive faces. Situation or Item: Highlight the main situation or item that is central to the video's content. Text: Include large, bold text that is easy to read. Expressive Face: Ensure the person or people have exaggerated facial expressions to convey strong emotions. Large Bold Text: Use text that is prominent and stands out against the background for easy readability. Clear Focus: Ensure that the main subjects of your thumbnail (person, situation/item, and text) are the focal points and easily distinguishable. Contrast: Use contrasting colors and elements to make the text and main subjects stand out from the background. Simplicity: Keep the design simple and uncluttered so that the main elements are easily identifiable. Quality: Use high-resolution images and graphics to ensure your thumbnail looks professional and clear on all devices. Dramatic Sticker Effects: Emphasizing the main subject with pronounced sticker effects to make it stand out prominently against the background. Vibrant Background with High Contrast: Choosing dynamic, contrasting backgrounds to ensure the thumbnail pops and grabs attention. Intriguing Elements: Incorporating elements like arrows to spark curiosity and hint at the video's content. Sense of Urgency or Challenge: Crafting titles that create excitement and urgency, making the content feel unmissable.) 

Ideation Mode: This mode is activated when the user selects "Ideation." 
Initial Prompt:
"IDEATION MODE ACTIVATED. Based on your channel niche, I'll generate some video ideas for you. Would you like to focus on any particular aspect or sub-topic within your niche?"
The bot will ask for the channel's niche and, based on the user's response, generate 10 specific video ideas, giving just the video's title. The user can ask for more options or a more detailed explanation of the ideas listed.

Script Mode: This mode is activated when the user types "SCRIPT MODE" 
Initial Prompt:
"SCRIPT MODE ACTIVATED. Considering your channel's focus, what specific topic would you like me to create a script for?"
Functionality:
Ask the user for the topic input.
Deliver the script in paragraph form without action lines, approximately 5,000 words.

Trendjack Mode:
Initial Prompt:
"TRENDJACK MODE ACTIVATED. Given your channel's focus, I'll search for upcoming events or trends that could be relevant. Would you like me to focus on any particular aspect of your niche?"

This mode should ask for the channel's niche and then, if available, search the web for upcoming events or dates correlating with the channel Niche. This could be things like an upcoming product launch for video production, a new update to piece of software, or even a holiday like Christmas. Be simple and direct with your response. Give a bold title of the event, and then a small 2-3 sentence paragraph about the event. Provide 5-6 events. Make sure to provide the date or date range of the events, and aim to only pull things that are coming up within the next 2 months. Prioritize launches and software updates. You need to confirm any events you pull by searching the web before providing them. 

SEO Guidelines Keywords: Use the main keyword at the beginning of the title and description, and incorporate secondary keywords throughout. Capitalization: Capitalize the first letter of each word in titles. Value Proposition: Clearly state the value proposition in descriptions. Use Numbers and Emotion in Titles: Boost CTR with numbers and emotionally charged words. Avoid Clickbait: Ensure titles accurately represent video content to avoid negative impacts on performance. Bot Interaction The bot should be straightforward and provide only the requested information. Button labels are "Ideation Mode", "Script Mode", "Trendjack Mode", and "SEO Mode". Initial responses should start with "(Chosen mode) ACTIVATED". Make the title of the chat the mode name, then a 3-word synopsis of the chat topic.`;
  };


  const handleAnalyze = async () => {
    if (!channelUrl.trim()) {
      alert('Please enter a YouTube channel URL');
      return;
    }

    setIsLoading(true);
    setShowResults(false);
    setCopySuccess(false);
    setShowLoadingModal(false);

    try {
      const channelId = await getChannelId(channelUrl);
      const [data, recent, popular] = await Promise.all([
        getChannelData(channelId),
        getRecentVideos(channelId),
        getPopularVideos(channelId)
      ]);

      const generatedInstructions = generateInstructions(data, recent, popular);

      setChannelData(data);
      setRecentVideos(recent);
      setPopularVideos(popular);
      setInstructions(generatedInstructions);

      // Show loading modal for 2.5 seconds before displaying results
      setShowLoadingModal(true);
      await new Promise(resolve => setTimeout(resolve, 2500));
      setShowLoadingModal(false);
      setShowResults(true);
    } catch (error) {
      console.error('Error:', error);
      alert(`Error analyzing channel: ${error instanceof Error ? error.message : 'Unknown error'}`);
      setShowLoadingModal(false);
    } finally {
      setIsLoading(false);
    }
  };

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(instructions);
      setCopySuccess(true);
      setTimeout(() => setCopySuccess(false), 2000);
    } catch (error) {
      console.error('Failed to copy:', error);
      alert('Failed to copy instructions. Please try again.');
    }
  };

  const formatNumber = (num: number): string => {
    if (num >= 1000000) {
      return (num / 1000000).toFixed(1) + 'M';
    }
    if (num >= 1000) {
      return (num / 1000).toFixed(1) + 'K';
    }
    return num.toLocaleString();
  };

  const seoConfig = toolsSEO['channel-consultant'];
  const schemaData = generateToolSchema('channel-consultant', seoConfig, [
    { question: 'Which AI platform should I use the generated instructions with?', answer: 'The instructions work with any modern AI assistant  ChatGPT (GPT-4o or later), Claude (claude.ai), Google Gemini, or any AI that accepts a system-level custom instruction or custom GPT configuration. Claude and ChatGPT\'s GPT-4o are recommended for the highest quality output.' },
    { question: 'What is the difference between the four modes?', answer: 'SEO Mode generates titles, descriptions, tags, and thumbnail concepts from a video transcript or topic. Ideation Mode produces 10 video ideas tailored to your niche. Script Mode writes a full 5,000-word script on any topic you specify. Trendjack Mode searches for upcoming events, product launches, and trends relevant to your content area.' },
    { question: 'How often should I regenerate my consultant?', answer: 'Regenerate your consultant whenever your channel undergoes a significant change  a niche pivot, a major subscriber milestone, a shift in upload frequency, or a change in your top-performing content type. For most creators, regenerating every 23 months keeps the consultant current.' },
    { question: 'Is my channel data stored or shared?', answer: 'No. Your channel data is fetched from the YouTube public API in real time and used only to generate the bot instructions text. No data is stored on our servers. The generated instructions text exists only in your browser session and clipboard.' }
  ]);


  return (
    <>
      <SEO
        title={seoConfig.title}
        description={seoConfig.description}
        keywords={seoConfig.keywords}
        canonical="https://youtool.io/tools/channel-consultant"
        schemaData={schemaData}
      />

      <S.PageWrapper>
      <S.MainContainer>
        <S.BackButton onClick={() => navigate('/tools')}>
          <i className="bx bx-arrow-back"></i>
          Back to Tools
        </S.BackButton>

        {/* Enhanced Header Section */}
        <S.EnhancedHeader backgroundImage={toolConfig.image}>
          <S.HeaderOverlay />
          <S.HeaderContent>
            <S.ToolIconContainer>
              <i className={toolConfig.icon}></i>
            </S.ToolIconContainer>

            <S.HeaderTextContent>
              <S.ToolTitle>{toolConfig.name}</S.ToolTitle>
              <S.ToolDescription>{toolConfig.description}</S.ToolDescription>

              <S.FeaturesList>
                {toolConfig.features.map((feature, index) => (
                  <S.FeatureItem key={index}>
                    <i className="bx bx-check-circle"></i>
                    <span>{feature}</span>
                  </S.FeatureItem>
                ))}
              </S.FeaturesList>

              {/* Integrated Search Bar */}
              <S.HeaderSearchContainer>
                <S.HeaderSearchBar>
                  <S.HeaderSearchInput
                    type="text"
                    value={channelUrl}
                    onChange={(e) => setChannelUrl(e.target.value)}
                    placeholder="Enter your channel URL for consultation"
                    onKeyPress={(e) => e.key === 'Enter' && handleAnalyze()}
                  />
                  <S.HeaderSearchButton onClick={handleAnalyze} disabled={isLoading}>
                    {isLoading ? (
                      <i className='bx bx-loader-alt bx-spin'></i>
                    ) : (
                      <i className='bx bx-search'></i>
                    )}
                  </S.HeaderSearchButton>
                </S.HeaderSearchBar>
              </S.HeaderSearchContainer>
            </S.HeaderTextContent>
          </S.HeaderContent>
        </S.EnhancedHeader>

        {/* Google Ad Spot */}
        <GoogleAd adSlot="1234567890" />

        {/* Educational Content Section */}
        {!showResults && !isLoading && (
          <S.EducationalSection>
            <S.EducationalContent>
              <S.SectionSubTitle>How to Use the Channel Consultant</S.SectionSubTitle>

              <S.EducationalText>
                Our Channel Consultant creates a custom AI assistant trained specifically on your YouTube channel.
                The AI understands your niche, audience, and branding to provide personalized content strategy,
                SEO optimization, and creative assistance tailored to your unique channel goals.
              </S.EducationalText>

              <S.StepByStep>
                <S.StepItem>
                  <S.StepNumberCircle>1</S.StepNumberCircle>
                  <S.StepContent>
                    <S.StepTitle>Enter Channel URL</S.StepTitle>
                    <S.EducationalText>
                      Paste your YouTube channel URL in the search field above. Our system analyzes your
                      channel details, category, description, and subscriber metrics to understand your
                      content focus and audience demographics.
                    </S.EducationalText>
                  </S.StepContent>
                </S.StepItem>

                <S.StepItem>
                  <S.StepNumberCircle>2</S.StepNumberCircle>
                  <S.StepContent>
                    <S.StepTitle>AI Bot Creation</S.StepTitle>
                    <S.EducationalText>
                      The system generates comprehensive instructions that train an AI chatbot to become
                      your personal YouTube consultant. The bot understands your niche and can operate
                      in four specialized modes: SEO, Ideation, Script, and Trendjack.
                    </S.EducationalText>
                  </S.StepContent>
                </S.StepItem>

                <S.StepItem>
                  <S.StepNumberCircle>3</S.StepNumberCircle>
                  <S.StepContent>
                    <S.StepTitle>Deploy Your Consultant</S.StepTitle>
                    <S.EducationalText>
                      Copy the generated instructions and paste them into any AI platform like ChatGPT,
                      Claude, or Gemini. Your custom consultant will immediately understand your channel
                      and provide expert advice tailored to your specific content strategy needs.
                    </S.EducationalText>
                  </S.StepContent>
                </S.StepItem>
              </S.StepByStep>
            </S.EducationalContent>

            <S.EducationalContent>
              <S.SectionSubTitle>AI Consultant Modes</S.SectionSubTitle>

              <S.FeatureList>
                <S.FeatureListItem>
                  <i className="bx bx-check-circle"></i>
                  <span><strong>SEO Mode:</strong> Generates optimized titles, descriptions, tags, and thumbnail concepts based on video content</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-check-circle"></i>
                  <span><strong>Ideation Mode:</strong> Creates video ideas and content concepts specifically aligned with your channel niche</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-check-circle"></i>
                  <span><strong>Script Mode:</strong> Writes detailed 5,000-word scripts for any topic within your content area</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-check-circle"></i>
                  <span><strong>Trendjack Mode:</strong> Identifies upcoming events, launches, and trends relevant to your niche for timely content</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-check-circle"></i>
                  <span><strong>Personalized Insights:</strong> All recommendations consider your channel's specific category, audience, and content style</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-check-circle"></i>
                  <span><strong>Multi-Platform Support:</strong> Works with ChatGPT, Claude, Gemini, and other AI platforms</span>
                </S.FeatureListItem>
              </S.FeatureList>

              <S.EducationalText>
                Transform any AI chatbot into your personal YouTube strategist. The consultant understands
                your unique channel identity and provides advice that aligns with your brand voice, target
                audience, and growth objectives. Get professional-level content strategy without the cost
                of hiring a dedicated YouTube consultant.
              </S.EducationalText>
            </S.EducationalContent>

            <S.EducationalContent>
              <S.SectionSubTitle>Frequently Asked Questions</S.SectionSubTitle>

              <S.FeatureList>
                <S.FeatureListItem>
                  <i className="bx bx-help-circle"></i>
                  <span><strong>Which AI platform should I use the generated instructions with?</strong> The instructions work with any modern AI assistant  ChatGPT (GPT-4o or later), Claude (claude.ai), Google Gemini, or any AI that accepts a system-level custom instruction or "custom GPT" configuration. For best results, use a model that supports long context windows, as the generated instructions are detailed. Claude and ChatGPT's GPT-4o are recommended for the highest quality output.</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-help-circle"></i>
                  <span><strong>What is the difference between the four modes?</strong> SEO Mode generates titles, descriptions, tags, and thumbnail concepts from a video transcript or topic. Ideation Mode produces 10 video ideas tailored to your niche. Script Mode writes a full 5,000-word script on any topic you specify. Trendjack Mode searches for upcoming events, product launches, and trends relevant to your content area that you can create timely videos about.</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-help-circle"></i>
                  <span><strong>How often should I regenerate my consultant?</strong> Regenerate your consultant whenever your channel undergoes a significant change  a niche pivot, a major subscriber milestone, a shift in upload frequency, or a change in your top-performing content type. For most creators, regenerating every 23 months keeps the consultant current with your channel's growth and direction.</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-help-circle"></i>
                  <span><strong>Does the tool work for brand new channels with no videos?</strong> The consultant works best when your channel has at least a few videos, since it analyzes your recent uploads and top performers to personalize recommendations. For brand new channels, you can still generate instructions  the bot will use your channel description and category to inform its advice, though it will have less historical data to draw from.</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-help-circle"></i>
                  <span><strong>Is my channel data stored or shared?</strong> No. Your channel data is fetched from the YouTube public API in real time and used only to generate the bot instructions text. No data is stored on our servers. The generated instructions text exists only in your browser session and clipboard.</span>
                </S.FeatureListItem>
              </S.FeatureList>
            </S.EducationalContent>

            <S.EducationalContent>
              <S.SectionSubTitle>Related Tools</S.SectionSubTitle>

              <S.FeatureList>
                <S.FeatureListItem>
                  <i className="bx bx-link"></i>
                  <span><a href="/tools/channel-analyzer"><strong>Channel Analyzer</strong></a>  Get a full analytics report on your channel's SEO score, engagement rate, consistency, and branding before running your consultant session.</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-link"></i>
                  <span><a href="/tools/youtool-playbooks"><strong>YouTool Playbooks</strong></a>  Browse a library of pre-built AI prompt playbooks for YouTube creators covering SEO, scripting, ideation, and more.</span>
                </S.FeatureListItem>
                <S.FeatureListItem>
                  <i className="bx bx-link"></i>
                  <span><a href="/tools/tag-generator"><strong>Tag Generator</strong></a>  Use SEO Mode output from your consultant alongside the Tag Generator to build a complete, optimized tag set for your videos.</span>
                </S.FeatureListItem>
              </S.FeatureList>
            </S.EducationalContent>
          </S.EducationalSection>
        )}

        <S.ResultsContainer className={showResults ? 'visible' : ''}>
          {isLoading ? (
            <S.LoadingContainer>
              <i className='bx bx-loader-alt bx-spin'></i>
              <p>Creating custom bot instructions...</p>
            </S.LoadingContainer>
          ) : channelData ? (
            <S.ResultCard>
              <S.SuccessHeader>
                <i className="bx bx-check-circle"></i>
                <S.SuccessTitle>Custom Bot Created Successfully!</S.SuccessTitle>
              </S.SuccessHeader>

              <S.ChannelInfo>
                <S.ChannelLogoContainer>
                  <S.ChannelLogo
                    src={
                      channelData.snippet.thumbnails?.high?.url ||
                      channelData.snippet.thumbnails?.medium?.url ||
                      channelData.snippet.thumbnails?.default?.url ||
                      'https://via.placeholder.com/200x200?text=Channel'
                    }
                    alt="Channel Profile"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.src = 'https://via.placeholder.com/200x200?text=Channel';
                    }}
                  />
                </S.ChannelLogoContainer>
                <S.ChannelDetails>
                  <S.ChannelName>{channelData.snippet.title}</S.ChannelName>
                  <S.SubscriberCount>
                    <i className="bx bx-user"></i>
                    {formatNumber(parseInt(channelData.statistics.subscriberCount))} subscribers
                  </S.SubscriberCount>
                </S.ChannelDetails>
              </S.ChannelInfo>

              <S.BotsSection>
                <S.SectionTitle>
                  <i className="bx bx-cog"></i>
                  Available Bot Modes
                </S.SectionTitle>

                <S.ModesGrid>
                  <S.ModeCard>
                    <S.ModeIcon className="bx bx-search-alt-2"></S.ModeIcon>
                    <S.ModeTitle>SEO MODE</S.ModeTitle>
                    <S.ModeDescription>Generates optimized titles, descriptions, tags, and thumbnails</S.ModeDescription>
                  </S.ModeCard>

                  <S.ModeCard>
                    <S.ModeIcon className="bx bx-bulb"></S.ModeIcon>
                    <S.ModeTitle>IDEATION MODE</S.ModeTitle>
                    <S.ModeDescription>Creates video ideas based on channel niche and trending topics</S.ModeDescription>
                  </S.ModeCard>

                  <S.ModeCard>
                    <S.ModeIcon className="bx bx-edit-alt"></S.ModeIcon>
                    <S.ModeTitle>SCRIPT MODE</S.ModeTitle>
                    <S.ModeDescription>Writes detailed 5,000-word scripts for specific topics</S.ModeDescription>
                  </S.ModeCard>

                  <S.ModeCard>
                    <S.ModeIcon className="bx bx-trending-up"></S.ModeIcon>
                    <S.ModeTitle>TRENDJACK MODE</S.ModeTitle>
                    <S.ModeDescription>Finds upcoming events and trends relevant to your niche</S.ModeDescription>
                  </S.ModeCard>
                </S.ModesGrid>
              </S.BotsSection>

              <S.ActionsSection>
                <S.InstructionsText>
                  Bot instructions generated successfully! Copy and paste into your AI platform:
                </S.InstructionsText>

                <S.CopyButton onClick={handleCopy} success={copySuccess}>
                  {copySuccess ? (
                    <>
                      <i className="bx bx-check"></i>
                      Copied Successfully!
                    </>
                  ) : (
                    <>
                      <i className="bx bx-copy"></i>
                      Copy Bot Instructions
                    </>
                  )}
                </S.CopyButton>

                <S.AILinksSection>
                  <S.AILinksTitle>Popular AI Platforms:</S.AILinksTitle>
                  <S.AILinks>
                    <S.AIButton
                      href="https://chat.openai.com/"
                      target="_blank"
                      rel="noopener noreferrer"
                      color="#10a37f"
                    >
                      <i className="bx bx-bot"></i>
                      ChatGPT
                    </S.AIButton>
                    <S.AIButton
                      href="https://gemini.google.com/chat"
                      target="_blank"
                      rel="noopener noreferrer"
                      color="#4285f4"
                    >
                      <i className="bx bx-diamond"></i>
                      Gemini
                    </S.AIButton>
                    <S.AIButton
                      href="https://claude.ai/new"
                      target="_blank"
                      rel="noopener noreferrer"
                      color="#ff6b35"
                    >
                      <i className="bx bx-brain"></i>
                      Claude
                    </S.AIButton>
                  </S.AILinks>
                </S.AILinksSection>
              </S.ActionsSection>

            </S.ResultCard>
          ) : null}
        </S.ResultsContainer>

        {/* Loading Modal */}
        {showLoadingModal && (
          <S.LoadingModal>
            <S.LoadingModalContent>
              <S.LoadingSpinner>
                <i className='bx bx-loader-alt bx-spin'></i>
              </S.LoadingSpinner>
              <S.LoadingModalText>Generating custom bot instructions...</S.LoadingModalText>
              <S.LoadingModalSubtext>Analyzing channel data and creating personalized AI consultant</S.LoadingModalSubtext>
            </S.LoadingModalContent>
          </S.LoadingModal>
        )}
      </S.MainContainer>
    </S.PageWrapper>
    </>
  );
};

export default ChannelConsultant;